// mode변경 test
import React, { useCallback, useState, useEffect }  from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { ADD_POST_REQUEST } from '../reducers/post';

import '../components/Contents.css';
import Link from 'next/link';
import '../components/Menu.css';
import '../components/reset.css';
import { Router } from 'next/router';

  
const Cont = () => {
    const dispatch = useDispatch();
    const [text, setText ] = useState('');
    const { imagePaths, isAddingPost, postAdded, mainPosts } = useSelector(state => state.post);
    //메뉴클릭시 컨텐츠 변경
    const [ Mode, setMode ] = useState('read');

    const handleChangePage = (e) => {
        e.preventDefault();
        setMode('member');
        let classa = document.querySelector(".menu_a")
        let classm = document.querySelector(".menu_m")
        classa.setAttribute("id","");
        classm.setAttribute("id","on");
    };
    const handleChangePage2 = (e) => {
        e.preventDefault();
        setMode('read');
        let classa = document.querySelector(".menu_a")
        let classm = document.querySelector(".menu_m")
        classa.setAttribute("id","on");
        classm.setAttribute("id","");
    };

    useEffect(() => {
        setText('');
    },[postAdded === true]);

    const onSubmitForm = useCallback((e) => {
        e.preventDefault();
        dispatch({
            type: ADD_POST_REQUEST,
            data: {
                text,
            },
        });
    }, []);

    const handleChange = (event) => {
        setFile({
          file:URL.createObjectURL(event.target.files[0])
        });
    };

    //단일이미지 미리보기
    const [img, setImg] = useState(null);
    const onChangeImage = useCallback((e)=>{
        var reader = new FileReader();
        reader.readAsDataURL(e.target.files[0]);
        reader.onloadend = function () {
            setImg(reader.result)
          }
    },[]);

    const onChangeText = useCallback((e) => {
        setText(e.target.value);
        //console.log();
    }, []);

    // const uploadFileReader =()=>{
    //     var uploadFile = document.getElementsByClassName('uploadBtn');
    //     if(window.FileReader){
    //          var filename = uploadFile[0].files[0].name;
    //      } else {
    //          var filename = uploadFile.val().split('/').pop().split('\\').pop();
    //      }
    
    //     document.getElementsByClassName('fileName').val(filename);
    // };
  

    if(Mode==='member'){
        return(
        <>
         <div className="top">
                <ul className="menubox">
                    <li className="menu_a" id="on" onClick={handleChangePage2}>전체글</li>
                    <li className="menu_m"  onClick={handleChangePage}>멤버</li>
                </ul>           
            </div>
            <div className="wrap">
                <div className="groupinfo">
                    <div className="groupimg"></div>
                    <div className="grouptext">
                        <h1>댕댕이집사 그룹</h1>
                        <p className="numMem">Member. 43</p>
                        <p>세젤예 댕댕이들 모여라~!</p>
                        <nav className="groupSubmitBtn">
                            <button className="gjoin">가입하기</button>
                            <button className="gout">그룹탈퇴</button>
                        </nav>
                    </div>
                </div>
               
                <div className="memberbox">
                    멤버목록
                </div>
            </div>
        </>
        );
      };

    if(Mode==='read')
    return (
        <>
            <div className="top">
                <ul className="menubox">
                    <li className="menu_a" id="on" onClick={handleChangePage2}>전체글</li>
                    <li className="menu_m"  onClick={handleChangePage}>멤버</li>
                </ul>           
            </div>
            <div className="wrap">
                <div className="groupinfo">
                    <div className="groupimg"></div>
                    <div className="grouptext">
                        <h1>댕댕이집사 그룹</h1>
                        <p className="numMem">Member. 43</p>
                        <p>세젤예 댕댕이들 모여라~!</p>
                        <nav className="groupSubmitBtn">
                            <button className="gjoin">가입하기</button>
                            <button className="gout">그룹탈퇴</button>
                        </nav>
                    </div>
                </div>
                
                <form className="uploadTb" encType="multipart/form-data" onSubmit={onSubmitForm}>
                    <div className='row1'>
                        <textarea maxLength={1500} placeholder="소식을 남겨주세요"
                                  className="tarea" value={text} onChange={onChangeText} />
                    </div>

                    <div className='row2'>
                        <div className="icon01">
                            <label for="uploadBtn" className="btn_file"></label>
                            <input id="img_file" type="file" name="file" accept=".jpg, .jpeg, .png" 
                                onChange={onChangeImage} />
                        </div>
                        <div className="icon02">
                            <label for="uploadBtn"></label>
                            <input id="mv_file" type="file" name="mv_file" 
                                   accept=".mp4, .wmv, .avi" onClick={onChangeImage} />
                        </div>
                        <div className="icon03">
                            <label for="file-input"></label>
                            <input id="doc_file" type="file" accept=".txt" onChange={onChangeImage} />
                        </div>
                        <div className="icon04">
                            <label Htmlfor="file-input"></label>
                            <input type="submit" id="file-input" value="" loading={isAddingPost} />
                        </div>
                        
                    </div>
                </form>
                {/* 게시물올라갈부분 */}
                <div className="letsbegin" >
                    그룹에 재미있는 이야기를 써보세요.
                    {/* <div post={c}></div> */}
                </div>
                {/* 더보기버튼 */}
                <button className="more">더보기</button>
                
                <div class="fileBox" >
                    <label for="uploadBtn" className="btn_file" > </label>
                    <input type="file" id="uploadBtn" className="uploadBtn" onChange={onChangeImage}  />
                </div>
            </div>
            
        </>
    );
};

export default Cont;

